.contact form div {
    display: flex;
}

.contact textarea {
    border: 1px solid rgba(0, 0, 0, 0.1);
    width: 100%;
    border-radius: 5px;
    margin-bottom: 20px;
    resize: none;
}

.contact input {
    margin-bottom: 20px;
    margin-right: 10px;
}

.contact form {
    padding: 30px;
}

@media screen and (max-width: 768px) {
    .contact h4 {
        width: 100%;
        margin-bottom: 30px;
    }
    .contact form div {
        flex-direction: column;
    }
    .contact input {
        width: 100%;
    }
}